<template>
  <div class="hello">
    <div class="signup-modal" v-show="showSignUp">
      <div class="modal-content">
        <div class="close" @click="closeSignUp()">x</div>

      </div>
    </div>
    <div class="navbar">
      <div>
        <img class="app-logo" src="@/assets/logo.png" />
      </div>
      <!-- <div class="nav-item" @click="openSignup()">
        Sign up
      </div> -->
    </div>
    <div class="banner">
      <div class="logo-container"></div>
      <div class="logo-container-tint"></div>
      <label class="app-announcement">
        Easiest way to find Recreational and Medical Cannabis around you.
      </label>
      <div class="coming-soon-container">
        <img class="coming" src="@/assets/googleplay.png" />
        <img class="coming" src="@/assets/appstore2.png" />
      </div>
      <form name="submit-to-google-sheet">
          <div style="color: #fff;
    font-size: 20px;
    font-weight: 600;
    padding-bottom: 10px;">Shipping soon! Want an update when we ship this?</div>
          <input v-model="email" name="email" type="email" placeholder="Email" required>
            <button type="submit">
              Submit
              <div v-if="loading" class="full-loader">
                  <div class="layout">
                    <div class="loader">
                        <div class="quad-1 quad"></div>
                        <div class="quad-2 quad"></div>
                        <div class="quad-3 quad"></div>
                        <div class="quad-4 quad"></div>
                    </div>
                    </div>
              </div>
            </button>
        </form>
    </div>
    <div class="herbie-phone">
      <img class="phone" src="@/assets/herbie-phone.png" />
    </div>
    <footer>
      Made with 
      <span>
        <img style="    max-width: 35px;
    height: auto;" src="@/assets/madegif.gif" />
      </span>
      at
      <a style="margin-left: 10px;
    text-decoration: none;
    color: #9871e4;" href="http://f22labs.com/">F22 Labs</a>
    </footer>
  </div>
</template>

<script>
import toastr from 'toastr'
export default {
  name: 'HelloWorld',
  data () {
    return {
      showSignUp: false,
      email: "",
      loading: false
    }
  },
  mounted() {
    // toastr.success('Have fun storming the castle!')
  const scriptURL = 'https://script.google.com/macros/s/AKfycbyMR4Ij1p4D23yzOH_d8YVq9Za8pxitawui8OnxMaiLWx10Axs/exec'
  const form = document.forms['submit-to-google-sheet']

  form.addEventListener('submit', e => {
    e.preventDefault()
    this.loading = true;
    fetch(scriptURL, { method: 'POST', body: new FormData(form)})
      .then(response => {
        toastr.success('We will keep you posted!')
        this.email = ""
        this.loading = false
      })
      .catch(error => console.error('Error!', error.message))
  })
  },
  methods: {
    openSignup() {
      this.showSignUp = true;
    },
    closeSignUp() {
      this.showSignUp = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.close {
  font-weight: 600;
  font-family: 'Montserrat';
  position: absolute;
  right: 15px;
  top: 10px;
  font-size: 18px;
  opacity: 0.7;
  cursor: pointer;
}

.signup-modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  position: relative;
  width: 700px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  box-shadow: 3px 3px 7px 7px rgba(0,0,0,0.1);
}

.modal-content input {
  width: 500px;
  height: 30px;
  padding-left: 10px;
  font-family: 'Montserrat';
  font-size: 18px;
  margin-top: 20px;
  border-radius: 5px;
  outline: none !important;
  background: #fff;
  box-shadow: none;
}

.modal-content button {
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
  padding: 10px 34px;
  border-radius: 5px;
  font-family: 'Montserrat';
  font-weight: 600;
  background-color: #3fc380;
  color: #fff;
}

footer {
  height: 100px;
  background-color: #3fc380;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
}

form, .app-announcement, .coming-soon-container {
  z-index: 10;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  font-family: 'Montserrat';
  font-weight: 600;
  align-items: center;
  color: #3fc380;
}

.modal-content label {
  font-family: 'Lato';
  font-weight: 600;
  align-items: center;
  color: #3fc380;
  margin-bottom: 30px;
}

.herbie-phone {
  display: flex;
  justify-content: center;
}

.coming-soon-container > img {
  max-width: 170px;
  height: auto;
  opacity: 0.3;
}

.herbie-phone > img.phone {
  width: 100%;
  max-width: 1024px;
  height: auto;
}

.app-logo {
  max-width: 150px;
  height: auto;
}

.nav-item {
  cursor: pointer;
}

.app-announcement {
  font-family: 'Lato';
  font-size: 36px;
  font-weight: 900;
  color: #fff;
}

.banner {
  text-align: center;
  width: 100%;
  position: relative;
  background: #3fc380;
  display: flex;
  flex-direction: column;
  height: 300px;
  align-items: center;
  justify-content: space-around;
}

.logo-container {
  background-image: url("../assets/weed_banner.jpg");
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  opacity: 0.1;
  margin: 0 auto;
  position: absolute;
  top: 0;
}

.logo-container-tint {
  width: 100%;
  height: 300px;
  background-color: #3fc380;
  opacity: 0.1;
  margin: 0 auto;
  position: absolute;
  top: 0;
}

form input {
  width: 100vw;
  max-width: 300px;
  height: 27px;
  border-radius: 7px;
  border: 1px solid #f2f2f2;
  padding: 0 15px;
  font-size: 14px;
  font-family: Lato;
}

form button {
  width: 100px;
  height: 27px;
  border-radius: 7px;
  border: 1px solid #fff;
  background-color: #3db377;
  color: #fff;
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 14px;
  margin-top: 10px;
  position: relative;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

/*Loader Animation*/
.full-loader {
  position: absolute;
  top: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.full-loader body {
  pointer-events: none;
}
.full-loader > .layout {
  height: 100%;
  display: flex;
  align-items: center;
}

.loader {
  position: relative;
  margin: 0 auto;
  border-radius: 50%;
  width: 7px;
  height: 7px;
  animation-fill-mode: forwards;
}
.quad {
  position: absolute;
  top: 6px;
  right: 5px;
  display: block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  border: 2px solid transparent;
  border-left-color: #fff;
  animation-fill-mode: forwards;
}
.quad-1 {
  animation: rotate1 1200ms linear infinite;
}
.quad-2 {
  animation: rotate2 1200ms linear infinite;
}
.quad-3 {
  animation: rotate3 1200ms linear infinite;
}
.quad-4 {
  animation: rotate4 1200ms linear infinite;
}
@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate1 {
  0% {
    transform: rotateZ(0deg);
  }
  12.5% {
    transform: rotateZ(90deg);
  }
  25% {
    transform: rotateZ(180deg);
  }
  37.5% {
    transform: rotateZ(270deg);
  }
  50% {
    transform: rotateZ(310deg);
  }
  62.5% {
    transform: rotateZ(330deg);
  }
  75% {
    transform: rotateZ(340deg);
  }
  87.5% {
    transform: rotateZ(350deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate2 {
  0% {
    transform: rotateZ(0deg);
  }
  12.5% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(90deg);
  }
  37.5% {
    transform: rotateZ(180deg);
  }
  50% {
    transform: rotateZ(270deg);
  }
  62.5% {
    transform: rotateZ(330deg);
  }
  75% {
    transform: rotateZ(340deg);
  }
  87.5% {
    transform: rotateZ(350deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate3 {
  0% {
    transform: rotateZ(0deg);
  }
  12.5% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(20deg);
  }
  37.5% {
    transform: rotateZ(90deg);
  }
  50% {
    transform: rotateZ(180deg);
  }
  62.5% {
    transform: rotateZ(270deg);
  }
  75% {
    transform: rotateZ(340deg);
  }
  87.5% {
    transform: rotateZ(350deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate4 {
  0% {
    transform: rotateZ(0deg);
  }
  12.5% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(20deg);
  }
  32.5% {
    transform: rotateZ(30deg);
  }
  50% {
    transform: rotateZ(90deg);
  }
  62.5% {
    transform: rotateZ(180deg);
  }
  75% {
    transform: rotateZ(270deg);
  }
  87.5% {
    transform: rotateZ(350deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
</style>



